import * as React from 'react';
import { Link, graphql } from 'gatsby';

import Layout from '../components/layout';
import Seo from '../components/seo';

export default ({ data, location }) => {
  const post = data.post;
  const siteTitle = data.site.siteMetadata?.title || '';
  const { previous, next } = data;

  return (
    <div>
      <Seo
        title={post.frontmatter.title}
        description={post.frontmatter.description || `${post.excerpt}`}
      />
      <div className="row bg-dark" style={{height: 200, backgroundImage: `url("https://www.linearsystems.com/images/ls-blog_7.jpg")`}}>
        <header>
          {/* <h1>{post.frontmatter.title}</h1> */}
        </header>
      </div>

      <div className="container mt-5">
        <div className="row justify-content-center">
          <div className="col-8">
            <article
              className="blog-post"
              itemScope
              itemType="http://schema.org/Article"
            >
              <header className="mb-5">
                <h1 itemProp="headline">{post.frontmatter.title}</h1>
                <p>{post.frontmatter.date}</p>
              </header>
              <section
                dangerouslySetInnerHTML={{ __html: post.html }}
                itemProp="articleBody"
              />

              <hr className="mt-5" />

              <footer className="mt-4">
                {/* TODO: Some footer content here */}
                <span class="fst-italic">TODO: Footer</span>
              </footer>
            </article>

            <nav className="blog-post-nav mt-4">
              <ul
                style={{
                  display: 'flex',
                  flexWrap: 'wrap',
                  justifyContent: 'space-between',
                  listStyle: 'none',
                  padding: '20px 0 0 0',
                  margin: '20px 0 0 0'
                }}
              >
                <li>
                  {previous && (
                    <Link to={previous.fields.slug} rel="prev">
                      ← {previous.frontmatter.title}
                    </Link>
                  )}
                </li>
                <li>
                  {next && (
                    <Link to={next.fields.slug} rel="next">
                      {next.frontmatter.title} →
                    </Link>
                  )}
                </li>
              </ul>
            </nav>
          </div>
        </div>
      </div>
    </div>
  );
}

export const pageQuery = graphql`
  query BlogPostBySlug(
    $id: String!
    $previousPostId: String
    $nextPostId: String
  ) {
    site {
      siteMetadata {
        title
      }
    }
    post: markdownRemark(id: { eq: $id }) {
      id
      excerpt(pruneLength: 110)
      html
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        description
        thumbnailImageUrl
      }
    }
    previous: markdownRemark(id: { eq: $previousPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
    next: markdownRemark(id: { eq: $nextPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
  }`;
